@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap'); 
.App {
  text-align: center;
  font-family: "Noto Sans KR", sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
/* 사용자 입력 */
*{margin: 0; padding: 0; box-sizing: border-box;}
ul,ol,li{list-style: none;}
a{text-decoration: none; color: inherit;}
img{
  vertical-align: top; max-width: 100%;
}
.wicon{
  position: fixed; right: 30px; bottom: 20px;
  z-index: 300;
}
.wicon div{
  width: 50px; height: 50px;
  background: #FDFBF7;
  border-radius: 10px;
  margin-top: 20px;
  padding-top: 7px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .3);
  transition: all .4s;
}
.wicon div:hover{
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .6);
}
.wicon .cs img{
  width: 15px; height: 37px;
}
.wicon .top{
 line-height: 30px;
 font-size: 14px;
}
/* header */
#header{
  width: 100%; height: 100px;
  position: fixed; left: 0; top: 0;
  z-index: 9999;
}
.headIn{
  width: 1440px; height: 100px;
  margin: 0 auto;
}
#header:hover{
  background: #fff;
}
#header:hover #nav{
  color: #000;
}
#logo{
  position: absolute; left: 50%; top: 50%;
  transform: translate(-50%,-50%);
  width: 30px; height: 75px;
}
#nav{
  float: left;
  height: 100px;
  position: relative;
  color: #fff;
}
#nav>ul{
  display: flex;
  padding: 0; margin: 0;
  line-height: 115px;
  font-size: 14px;
  height: 100px;
}
#nav>ul>li{
  margin-right: 45px;
}
#nav>ul>li>a{
  display: block;
  width: 100%; height: 100%;
  position: relative;
}
#nav>ul>li>a:before{
  content: '';
  position: absolute; left: 0; top: 99%;
  width: 0; height: 2px;
  background: #333;
  transition: all .4s;
}
#nav>ul>li:hover>a:before{
  width: 100%;
}
#nav>ul .sub{
  font-size: 13px;
  position: absolute; left: 0; top: 100%;
  z-index: 100;
  width: 1440px; height: 50px;
  line-height: 50px;
  padding: 0;
  display: none;
  transition: all .3s;
}
#nav>ul li:nth-child(5) .sub{
  padding-left: 300px;
}
#nav>ul .sub>li{
  height: 50px;
  float: left;
  margin-right: 50px;
  color: rgba(0, 0, 0, .7);
  transition: all .3s;
}
#nav>ul .sub>li:hover{
  color:rgba(0, 0, 0, 1);
}
#nav>ul>li:hover .sub{
  display: block;
}
#nav>ul>li:hover .subBack{
  display: block;
}
#nav>ul .subBack{
  position: absolute; left: -240px; top: 100%;
  z-index: 99;
  width: 100vw; height: 50px;
  background: rgba(255, 255, 255, .7);
  display: none;
}
.util{
  float: right;
}
.util ul{
  display: flex;
  margin-top: 45px;
  font-size: 14px;
}
.util ul li a{
  display: block;
  width: 23px; height: 23px;
  margin-left: 25px;
  background: url(../public/img/icon/ser0.png) no-repeat center/cover;
  text-indent: -9999px;
}
.util ul li:nth-child(2) a{
  background-image: url(../public/img/icon/join0.svg);
}
.util ul li:nth-child(3) a{
  background-image: url(../public/img/icon/cart0.png);
}
#header:hover .util li:nth-child(1) a{
  background-image: url(../public/img/icon/ser1.png);
}
#header:hover .util li:nth-child(2) a{
  background-image: url(../public/img/icon/join1.svg);
}
#header:hover .util li:nth-child(3) a{
  background-image: url(../public/img/icon/cart1.png);
}
.kVisual{
  width: 100%; height: 839px;
  position: relative;
  top: -100px;
  overflow: hidden;
}
.kVisual a .ib{
  width: 100%; height: 839px;
}
.kVisual a .ib img{
   width: 100%; height: 839px;
   object-fit: cover;
}
.kVisual a .tb{
  position: absolute; top: 55%; left: 220px;
  text-align: left;
  color: #fff;
}
.kVisual a .tb p:nth-of-type(1){
  font-size: 55px;
  font-weight: 500;
}
.kVisual a .tb p:nth-of-type(2){
  font-size: 25px;
  margin-bottom: 40px;
}
.kVisual a .tb span{
  display: inline-block;
  font-size: 20px;
  padding: 10px;
  transition: all .3s;
}
.kVisual a .tb span:hover{
  box-shadow: 3px 3px 3px rgba(0, 0, 0, .5);
}
.kVisual .s-next,
.kVisual .s-prev{
  position: absolute; 
  width: 40px; height: 40px;
  z-index: 30;
  background: url(../public/img/arrow_l.png) no-repeat center/cover;
  border: none;
  text-indent: -9999px;
  filter: invert(100%);
}
.kVisual .s-next{
  top: 55%;
  right: 30px;
   transform: translateY(-50%);
  background-image: url(../public/img/arrow_r.png);
}
.kVisual .s-prev{
  left: 30px; top: 55%;
   transform: translateY(-50%);
}
#section{
  width: 1440px;
  margin: 0 auto;
}
#section h3{
  width: 100%; height: 60px;
  text-align: left;
  margin: 0;
}
#section .sec1{
  margin-top: 120px;
  height: 720px;
}
.sec1 .cont{
  width: 100%; height: 660px;
  padding-top: 20px;
  position: relative;
}
.sec1 .cont a{
  display: block;
  width: 464px; height: 620px;
  background: #FDFBF7;
  border-radius: 30px;
}
.sec1 .cont a .ib{
  width: 464px; height: 464px;
  overflow: hidden;
}
.sec1 .cont a .ib img{
  width: 464px; height: 464px;
  object-fit: cover;
  transition: all .4s;
}
.sec1 .cont a .tb{
  width: 464px; height: 154px;
  padding: 20px;
}
.sec1 .cont a .tb p:nth-child(1){
  font-size: 20px; 
  font-weight: 500;
  letter-spacing: 1px;
}
.sec1 .cont a .tb p:nth-child(2){
  color: #414141;
  letter-spacing: 2px;
}
.sec1 .cont .s-next,
.sec1 .cont .s-prev{
  position: absolute; top: 50%;
  transform: translateY(-50%);
  width: 40px; height: 40px;
  z-index: 30;
  background: url(../public/img/arrow_l.png) no-repeat center/cover;
  border: none;
  text-indent: -9999px;
}
.sec1 .cont .s-next{
  right: 30px;
  background-image: url(../public/img/arrow_r.png);
}
.sec1 .cont .s-prev{
  left: 30px;
}
.sec1 .cont a:hover img{
  transform: scale(1.1);
}
.mySwiper .swiper-scrollbar {
  bottom: -1px !important;
}
#section .sec2{
  height: 600px; 
  margin-top: 150px;
}
#section .sec2 .cont{
  width: 100%; height: 600px;
  background: url(../public/img/sec2.jpg) no-repeat center/cover;
  border-radius: 30px;
  position: relative;
}
.sec2 .cont .tb{
  position: absolute; left: 80px; top: 65%;
  transform: translateY(-50%);
  text-align: left;
  color: #fff;
}
.sec2 .cont .tb p:nth-of-type(1){
  font-size: 40px;
  margin-bottom: 15px;
  font-weight: 500;
}
.sec2 .cont .tb p:nth-of-type(2){
  font-size: 18px;
}
.sec2 .cont .tb span{
  display: inline-block;
  font-size: 16px;
  margin-top: 10px;
  padding: 15px 25px;
  transition: all .4s;
  cursor: pointer;
}
.sec2 .cont .tb span:hover{
  box-shadow: 3px 3px 3px rgba(0, 0, 0, .5);
}
.sec3{
  margin-top: 150px;
  height: 720px;
}
.sec3 .contWrap{
  display: flex; justify-content: space-between;
}
.sec3 .contWrap .cont{
  width: 708px; height: 720px;
  position: relative;
}
.sec3 .contWrap .cont .overT{
  position: absolute; left: 0; top: 0;
  z-index: 20;
  width: 100%; height: 600px;
  padding: 140px 50px 0 0;
  border-radius: 30px;
  background: rgba(0, 0, 0, .5);
  color: #fff;
  opacity: 0;
  transition: all .4s;
}
.sec3 .contWrap .cont:hover .overT{
  opacity: 1;
}
.sec3 .contWrap .cont .overT .ex{
  margin-bottom: 50px;
}
.sec3 .contWrap .cont a .overT .ex p:nth-child(1){
  font-weight: 500;
  font-size: 20px;
}
.sec3 .contWrap .cont a .overT .ex p:nth-child(2){
  font-size: 14px;
}
.sec3 .contWrap .cont .ib{
  width: 708px; height: 600px;
}
.sec3 .contWrap .cont .ib img{
  width: 708px; height: 600px;
  border-radius: 30px;
  object-fit: cover;
}
.sec3 .contWrap .cont .tb{
  width: 700px;
  height: 120px;
  padding: 20px 0;
}
.sec3 .contWrap .cont .tb p:nth-child(1){
  font-size: 25px;
  font-weight: 500;
}
.sec3 .contWrap .cont .tb p:nth-child(2){
  font-size: 14px;
  display: block;
  width: 135px; height: 45px;
  line-height: 30px;
  margin: 0 auto;
  padding: 5px 10px;
  border: 1px solid #000;
  border-radius: 5px;
  transition: all .4s;
  cursor: pointer;
}
.sec3 .contWrap .cont .tb p:nth-child(2):hover{
  background: rgba(0, 0, 0, .3);
  color: #fff;
}
.sec4{
  margin-top: 170px; 
  height: 610px;
  position: relative;
}
.sec4 .find{
  position: absolute; right: 0; top:5px;
}
.sec4 .find a{
  display: inline-block;
  padding: 3px 7px;
  border-radius: 4px;
  font-size: 14px;
  border-radius: 7px;
  transition: all .4s;
}
.sec4 .find a:hover{
  background: rgba(0, 0, 0, .3);
  color: #fff;
}
.sec4 .contWrap{
  height: 510px;
  overflow: hidden;
  display: flex; 
  gap: 20px;
}
.sec4 .contWrap .cont{
  height: 510px;
  transition: all .4s;
  flex: 1;
  position: relative;
}
.sec4 .contWrap .cont:hover{
  flex: 2;
}
.sec4 .contWrap .cont .ib{
 height: 500px; width: 100%;
}
.sec4 .contWrap .cont .ib img{
 height: 500px; width: 100%;
 object-fit: cover;
 border-radius: 30px;
}
.sec4 .contWrap .cont .tb{
  position: absolute; left: 0; bottom: 10px;
  width: 100%; height: 40px;
  font-size: 18px;
  color: #fff;
  transition: all .4s;
}
.sec4 .contWrap .cont:hover .tb{
  font-size: 22px;
  bottom: 15px;
  font-weight: 500;
}
#footer{
  margin-top: 150px;
  width: 100%; height: 350px;
  padding: 25px 0 0;
  border-top: 1px solid #666;
}
.footIn{
  width: 1440px; height: 300px;
  margin: 0 auto;
}
.footIn .ftop{
  height: 200px;
  padding-top: 50px;
  display: flex; justify-content: space-between;
}
.footIn .ftop .flogo{
  width: 35px; height: 75px;
}
.footIn .ftop .fmenu ul{
  display: flex;
  font-size: 14px;
}
.footIn .ftop .fmenu ul li{
  padding: 0 10px;
  border-right: 1px solid #666;
}
.footIn .ftop .fmenu ul li:nth-child(3){
  font-weight: 600;
}
.footIn .ftop .fmenu ul li:last-child{
  border: none;
}
.footIn .ftop .news{
  text-align: left;
  width: 320px;
  font-size: 14px;
}
.footIn .ftop .news p:nth-of-type(1){
  font-weight: 500;
}
.footIn .ftop .news p:nth-of-type(2){
  font-size: 12px;
  color: #333;
}
.footIn .ftop .news input{
  width: 250px;
  border: none;
  background: none;
  border-bottom: 1px solid #000;
  margin-right: 15px;
  padding: 7px;
}
.footIn .ftop .news button{
  border: none;
  background: none;
  width: 20px; height: 10px;
}
.footIn .fbottom{
  height: 100px;
  padding: 25px 0 0;
  display: flex;
  justify-content: space-between;
}
.footIn .fbottom .copy{
  color: #333;
  font-size: 12px;
  text-align: left;
}
.footIn .fbottom .snsset .snsset{
  margin-top: 40px;
}
.footIn .fbottom .snsset .sns a{
  display: inline-block;
  width: 30px; height: 30px;
  margin-right: 10px;
}
.footIn .fbottom .snsset .sns a:first-child{
  filter: invert(100%);
  width: 25px; height: 25px;
}
.footIn .fbottom .snsset .sns a:last-child{
  margin-right: 0;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
